import React,{useContext} from 'react'
import {Link,withRouter} from 'react-router-dom'

import './Group.scss'
import {MainContext} from '../../contexts/MainContext'

export default withRouter(function Group({group,location}){
	const {state}=useContext(MainContext)

	const {userId}=state
	const isAdmin=group.admins.indexOf(+userId)!==-1 // TODO: UserID Check
	const title=<>
		<img className="mr-12" src={require('../../assets/imgs/letter.png')} alt="Group icon"/>
		<h3>{group.title}</h3>
		<div className="h7 text-muted one-line">{group.bio}</div>
	</>
	return (
		<div className="Group row spc-16">
			{location.pathname.split('/').indexOf('group')===-1?
				<Link to={location.pathname+'/group/'+group.id} className="title col pb-12 pos-rel">{title}</Link>:
				<div className="title col pb-12 pos-rel">{title}</div>}
			{isAdmin?
				<Link to={location.pathname+'/edit-group/'+group.id} className="fa fa-ellipsis-v h5 super-center px-12"/>
				:undefined}
			<hr className="col-12 col-ext-y"/>
			{group.books.map(bookId=>{
				const b=state.BOOKS[bookId]
				return b?(
					<Link to={location.pathname+'/book/'+bookId} className="col-3 col-2-xl book" key={bookId}>
						<img className="col-12 card" src={b.thumbnail || require('../../assets/imgs/letter.png')} alt="PageBook placeholder"/>
						<div className="pt-8">
							<div className="one-line">{b.title}</div>
							<div className="h7 text-muted one-line">{b.detail}</div>
						</div>
					</Link>
				):undefined
			})}
			{isAdmin?<div className="pos-rel col-3 col-2-xl add-book f-self-start">
				<img className="col-12" src={require('../../assets/imgs/letter.png')} alt="PageBook placeholder"/>
				<div className="pos-abs stretch-to-fit super-center f-vert">
					<h3 className="mb-6">&#x002B;</h3>
					Add Book
				</div>
			</div>:undefined}
		</div>
	)
})
