import React from 'react'
import _ from 'lodash'

import './PageLogin.scss'
import SegmentControls from '../../utils/SegmentControls/SegmentControls'
import MyComponent from '../MyComponent'
import {MainContext,api} from '../../contexts/MainContext'
import Spinner from '../../utils/Spinner/Spinner'
import {setAlert} from '../../utils/Alert/Alert'

const initState=JSON.stringify({
	mode: 0,
	showSpinner: false,
})
export default class PageLogin extends MyComponent{
	static contextType=MainContext
	state=JSON.parse(initState)

	login=async e=>{
		e.preventDefault()
		this.setState({showSpinner: true})

		try{
			const loginResponse=await api('post','login',_.pick(this.state,['email','password']))
			this.context.dispatch(['LOGIN',loginResponse.data])
		}finally{this.setState({showSpinner: false})}
	}

	signUp=async e=>{
		e.preventDefault()
		this.setState({showSpinner: true})

		try{
			await api('post','register',_.pick(this.state,['email']))
			this.setState(JSON.parse(initState))
			setAlert(<div className="row spc-12" style={{maxWidth: '320px'}}>
				<div className="col-12 h5">&#x2713; Email Sent</div>
				<div className="col-12 h7 text-muted">
					We just sent an email to <b className="text-unmuted">{this.state.email}</b>.
					Check your email to complete your sign-up!
					<br/><br/>
					If you did not receive any emails from us, don't forget to check your spam folder!
				</div>
				<div className="bottom-row col-ext-x col-ext-b col-12 row spc-x-12 spc-y-8 f-justify-end">
					<button onClick={()=>setAlert()}>OK</button>
				</div>
			</div>)
		}finally{this.setState({showSpinner: false})}
	}

	render(){
		const {login,signUp}=this
		const {location,history}=this.props
		const {mode,showSpinner}=this.state
		const {version}=this.context.state

		if(localStorage.getItem('activeUserId')) // If logged in, bypass login screen
			history.replace(location.pathname.replace('/login',''))

		return (
			<div id="PageLogin" className="pos-abs stretch-to-fit">
				<div className="container row p-32">
					<div className="col">
						<h1 className="heading py-32">
							BBBook<sup className="h7 normal-caps ml-8" style={{height: 'auto'}}>Pre-Alpha {version}</sup>
						</h1>
					</div>
					<div className="card row spc-16 f-vert pos-rel" style={{width: '360px'}}>
						<SegmentControls value={mode}>
							<div className="super-center" onClick={()=>this.setState({mode: 0})}>Log In</div>
							<div className="super-center" onClick={()=>this.setState({mode: 1})}>Sign up</div>
						</SegmentControls>
						{mode===0?
							<form className="row spc-16 f-vert col col-ext pos-rel trim" onSubmit={login}>
								<div>
									<h1>Log In</h1>
									<div className="h7 text-muted">Enter the BBBook portal.</div>
								</div>
								<div className="col"/>
								<label required className="h7 my-0">Email</label>
								<input required {...this.twoWayBind('email')} name="email" type="email"/>
								<label required className="h7 my-0">Password</label>
								<input required {...this.twoWayBind('password')} name="password" type="password"/>
								{/*<Link className="button super-center" to="/dashboard">Log In</Link>*/}
								<div className="bottom-row col-ext-x col-ext-b text-right row f-vert spc-x-16 spc-y-12">
									<button>Log In</button>
									<hr className="col-ext-x"/>
									<div className="h7 text-right my-2" onClick={()=>this.setState({mode: 1})}>New to BBBook? Sign up!</div>
								</div>
							</form>
							:<form className="row spc-16 f-vert col col-ext pos-rel trim" onSubmit={signUp}>
								<div>
									<h1>Sign Up</h1>
									<div className="h7 text-muted">Enter the BBBook portal.</div>
								</div>
								<div className="col"/>
								<label required className="h7 my-0">Email</label>
								<input required {...this.twoWayBind('email')} name="email" type="email"/>
								{/*<Link className="button super-center" to="/dashboard">Sign Up</Link>*/}
								<div className="bottom-row col-ext-x col-ext-b text-right row f-vert spc-x-16 spc-y-12">
									<button>Sign Up</button>
									<hr className="col-ext-x"/>
									<div className="h7 text-right my-2" onClick={()=>this.setState({mode: 0})}>Have an account? PageLogin in!</div>
								</div>
							</form>
						}
					</div>
				</div>
				<Spinner {...{className: showSpinner?'active':''}}/>
			</div>
		)
	}
}
