import React,{useState} from 'react'
import {withRouter} from 'react-router-dom'

export let setAlert

// TODO: Maybe easier alert, prompt, confirm
// TODO: UseEffect scrollbar fix
export default withRouter(function Alert(){
	let alert
	[alert,setAlert]=useState()
	return (
		<div id="Alert" className={'modal'+(alert?' active':' ')}>
			{alert?<div style={{minWidth: 320,maxWidth: 'calc(100% - 24px)',maxHeight: 'calc(100% - 24px)'}}>
				<div className="close-btn" onClick={()=>setAlert()}/>
				{alert}
			</div>:undefined}
		</div>
	)
})