import React,{useContext,useEffect} from 'react'
import {Link,withRouter} from 'react-router-dom'
import {Scrollbar} from 'react-scrollbars-custom'

import './PageGroups.scss'
import Header from '../Header/Header'
import {MainContext,api} from '../../contexts/MainContext'
import Group from '../Group/Group'

export default withRouter(function PageGroups({location}){
	const {state,dispatch}=useContext(MainContext)
	useEffect(refresh,[location.pathname])
	const user=state.USERS[state.userId]

	function refresh(){
		if(!state.userId) return
		api('get','dashboard/'+state.userId).then(({data})=>{
			dispatch(['SET_DATA',data])
		}).catch(console.error)
	}

	return (
		<div id="PageGroups">
			<Scrollbar className="pos-abs stretch-to-fit">
				<div className="f-vert">
					<div className="container">
						{(user?.joined_groups || []).map(groupId=>{
							const group=state.GROUPS[groupId]
							return group?(
								<Group {...{key:groupId,group}}/>
							):undefined
						})}
					</div>
					<Header>
						<div className="col"/>
						<div className="icon icon-refresh super-center" onClick={refresh}/>
					</Header>
				</div>
			</Scrollbar>
			<Link className="button f-center f-horz-rev pos-abs create-group-btn p-0" to={location.pathname+'/edit-group/0'}>
				<i className="fa fa-plus super-center h5"/>
				<div className="super-center pl-24 pr-12 one-line">Create Group</div>
			</Link>
		</div>
	)
})
