import React,{useContext,useState} from 'react'
import {Link,withRouter} from 'react-router-dom'

import './Comment.scss'
import {MainContext,api} from '../../contexts/MainContext'
import {setAlert} from '../../utils/Alert/Alert'

const whitespaceRegex=/\s+/

// TODO: CommentBody
// TODO: Right-click
export default withRouter(function Comment({location,children,commentId,showChildren}){
	const {state,dispatch}=useContext(MainContext)
	const [editMode,setEditMode]=useState()

	const comment=state.COMMENTS[commentId]
	const user=state.USERS[comment?.user]
	const {userId}=state

	let commentBody

	if(comment){
		const selection=comment?.selection_data?JSON.parse(comment?.selection_data):[]
		commentBody=<>
			{/* TODO: selectionIndex data redesign */}
			{/* TODO: remove onCLick workaround after node.js backend */}
			{selection.map((s,i)=><blockquote
				key={i}
				className="h7 text-muted mt-2 row cur-p"
				onClick={()=>document.querySelector(`#a${commentId}0>span`)?.scrollIntoView({behavior: 'smooth'})}>
				<div className="trim-y col">{s?.text}</div>
			</blockquote>)}
			{/*userId+''===comment.user+''?<div className="h7 b-700" style={{marginBottom: '-21px !important'}}>
				<i className="fa fa-plus"/> Add Highlight
			</div>:undefined*/}
			<div className="h8 my-2 text-right">{(new Date(comment.created_at)).toLocaleString()}</div> {/* TODO: Beautify */}
			<div className="bottom-row col-ext-x col-ext-b row spc-8 spc-x-12 f-center">
				{/*<i className="icon icon-arrow_big_up" onClick={like}/><div className="h7 ml-0">{comment.liked.length}</div>
				<i className="icon icon-arrow_big_down text-muted"/><div className="h7 ml-0">{comment.disliked.length}</div>*/}
				{/*<i className="fa fa-star text-muted h6"/>*/}
				<div className="col"/>
				<i className="icon icon-reply h6"/><div className="h7 ml-0">{comment.replys.length}</div>
				<Link className="button super-center" to={location?.pathname+'/thread/'+commentId}>Open Thread</Link>
				{userId+''===comment.user+''?<>
					{editMode?<div className="edit-form-open pos-abs stretch-to-fit" onClick={()=>setEditMode(false)}/>:undefined}
					<div className="px-12 py-8 col-ext pos-rel f-self-stretch f-center cur-p" onClick={()=>setEditMode(true)}>
						<i className="fa fa-ellipsis-v"/>
						<div className={'edit-form pos-abs card trim'+(editMode?' active':' ')}>
							<div className="px-12 py-8 f-center" onClick={deleteComment}><i className="icon-cross_mark mr-8"/>Delete</div>
							{/*<div className="px-12 py-8">Edit</div>*/}
						</div>
					</div>
				</>:undefined}
			</div>
		</>
		return (
			<>
				<style>{`
					.custom-highlight-layer>.a${commentId}{ display: block !important }
				`}</style>
				<div className="Comment">
					<div id={`b${commentId}`} className="id-tag"/>
					<div className="row pb-4">
						{comment.hidden?undefined:<>
							<div className="h7 hashtag">{comment.hashtag.split(whitespaceRegex).map((h,i)=><span key={i} className="mr-4">#{h}</span>)}</div>
						</>}
						<div className="col"/>
						<div className="h7 text-muted">@{user.username}</div>
						<img className="pos-rel" src={require('../../assets/imgs/default-profile-icon.jpg')} alt="User icon"/>
					</div>
					<div className="card row f-vert spc-12">
						<div className="o ne-line">{comment.hidden?'[Deleted]':comment.body}</div>
						{comment.hidden?undefined:commentBody}
					</div>
				</div>
				{showChildren && (children || !!comment?.replys.length) &&
				<div className="reply col-ext row spc-12">
					<div className="col col-ext-x col-ext-t row spc-12 f-vert">
						{comment?.replys.map(c=><Comment key={c} {...{commentId: c,showChildren}}/>)}
						{children}
					</div>
				</div>
				}
			</>
		)
	}else return undefined

	function like(){
		api('put','like/'+commentId,undefined,{headers:{
			userid: userId
		}}).then(({data})=>{
			console.log(data)
		})
	}

	function deleteComment(){
		// TODO: Use commentBody
		setAlert(
			<div className="row spc-12" style={{maxWidth: '320px'}}>
				<div className="col-12">Do you want to delete this comment?</div>
				<div className="Comment card col-12 row f-vert spc-12">
					<div className="o ne-line">{comment.body}</div>
					<blockquote className="h7 text-muted mt-2 row">
						<div className="trim-y col">{comment.selection_data}</div>
					</blockquote>
					<div className="h8 my-2 text-right">{(new Date(comment.created_at)).toLocaleString()}</div> {/* TODO: Beautify */}
				</div>
				<div className="bottom-row col-ext-x col-ext-b col-12 row spc-x-12 spc-y-8 f-justify-end">
					<button className="outline" onClick={()=>setAlert()}>Cancel</button>
					<button onClick={()=>{
						api('delete','comment/'+commentId).then(({data})=>{
							setAlert()
							comment.hidden=true
							dispatch(['SET_DATA',{comments: [comment]}])
						})
					}}>OK</button>
				</div>
			</div>
		)
	}
})
