import React,{useContext} from 'react'
import {NavLink,withRouter} from 'react-router-dom'

import './Sidemenu.scss'
import {MainContext} from '../../contexts/MainContext'
import SegmentControls from '../../utils/SegmentControls/SegmentControls'

const urlToValue={
	dashboard: 0,
	groups: 1,
	// library: 1,
	// favorites: 1,
	// connections: 1,
	// settings: 1,
}

// MAYBE: Hover segment controls
export default withRouter(function Sidemenu({history,location}){
	const {state,dispatch}=useContext(MainContext)
	const user=state.USERS[state.userId]
	return (
		<section id="Sidemenu" className={'f-vert p-8 pos-abs stretch-to-fit'+(state.UI_SIDEMENU?' active':'')}>
			<div className="col"/>
			<div className="row f-vert spc-12 mb-12">
				<img className="profile-icon f-self-center" src={require('../../assets/imgs/default-profile-icon.jpg')} alt="default-profile-icon"/>
				<div className="text-center col-12" style={{width: 'calc(100% - 72px)'}}>
					<div className="one-line">
						Firstname Lastname
						{/*{user?user.first_name:'Firstname'}*/}
						{/*{user?user.last_name:'Lastname'}*/}
					</div>
					<div className="h7 text-muted">{user?user.username:'@Username'}</div>
				</div>
			</div>
			<SegmentControls className="f-vert" value={urlToValue[location.pathname.split('/')[1]] || 0} vertical={true}>
				<NavLink to="/dashboard" className="p-12">Dashboard</NavLink>
				{/*<Link to="/library" className="p-12">My PageDashboard</Link>*/}
				{/*<Link to="/favorites" className="p-12">Favorites</Link>*/}
				{/*<Link to="/connections" className="p-12">My Connections</Link>*/}
				<NavLink to="/groups" className="p-12">Groups</NavLink>
				{/*<Link to="/settings" className="p-12">Account settings</Link>*/}
				{/*<Link className="p-12">Switch account</Link>*/}
				{/*<Link to="" className="p-12">&nbsp;</Link>*/}
			</SegmentControls>
			<div className="col"/>
			<div className="h7 text-center mb-12" style={{cursor: 'pointer'}} onClick={logout}><i className="icon icon-signout"/> Logout</div>
		</section>
	)

	function logout(){
		dispatch(['LOGOUT'])
		history.push('/login')
	}
})