import React,{useContext,useEffect,useState} from 'react'
import {withRouter,Route,Link} from 'react-router-dom'
import {Scrollbar} from 'react-scrollbars-custom'

import './PageBook.scss'
import Header from '../Header/Header'
import SegmentControls from '../../utils/SegmentControls/SegmentControls'
import tabStart from '../../assets/imgs/tab-start.svg'
import Comment from '../Comment/Comment'
import CommentThread from '../CommentThread/CommentThread'
import PdfVerticalScroll from '../PdfVerticalScroll/PdfVerticalScroll'
import {MainContext,api} from '../../contexts/MainContext'
import Spinner from '../../utils/Spinner/Spinner'
import NewQuestionComment from '../NewQuestionComment/NewQuestionComment'
import pdfjs from '../../utils/PDFRenderer/PDFRenderer'

export const sidebarModes=['question','comment','note','highlight']
export const publicPrivateModes={
	'question' : 'public',
	'comment'  : 'public',
	'note'     : 'private',
	'highlight': 'private',
}
export let setSidebarMode,setSelection
export default withRouter(function PageBook({history,match,location}){
	const {state,dispatch}=useContext(MainContext)
	let sidebarMode,selection
	;[sidebarMode,setSidebarMode]=useState('comment') // TODO: Change to path variable
	const publicPrivateMode=publicPrivateModes[sidebarMode]
	const [userFilter,setUserFilter]=useState(false)
	const [publicSearchString,setPublicSearchString]=useState('')
	;[selection,setSelection]=useState(undefined)
	const [pdf,setPdf]=useState(undefined)
	useEffect(()=>{componentDidMount()},[])
	useEffect(refresh,[location.pathname])

	const book=state.BOOKS[match.params.id]
	const group=state.GROUPS[book?.group_id] // TODO: Backend group_id
	const {userId}=state

	const categorized={
		'question' : [],
		'comment'  : [],
		'note'     : [],
		'highlight': [],
	} // Categorizing comments based on type
	book?.comment_set.forEach(c=>{ // eslint-disable-line no-unused-expressions
		c=state.COMMENTS[c]
		if(!c) return
		if(!categorized[c.typeof]) categorized[c.typeof]=[]
		if(!c.hidden) categorized[c.typeof].push(c)
	})
	const filtered=categorized[sidebarMode].filter(comment=>(
		(!userFilter || (userFilter || publicPrivateMode!=='public') && ''+comment.user===''+userId) && // eslint-disable-line
		(comment.hashtag.indexOf(publicSearchString)!==-1)
	))

	function refresh(){
		api('get','readingbook/'+match.params.id).then(({data})=>{
			dispatch(['SET_DATA',data])
		}).catch(console.error)
	}

	async function componentDidMount(){
		// console.log('PageBook componentDidMount')
		if(book?.docfile){
			const pdfLoadTask=pdfjs.getDocument(book.docfile)
			pdfLoadTask.onProgress=({loaded,total})=>console.log(loaded/total*100)
			// console.log(pdfLoadTask)
			const pdf=await pdfLoadTask.promise
			// console.log(pdf)
			const promises=new Array(pdf.numPages)
			for(let i=0;i<promises.length;++i) promises[i]=pdf.getPage(i+1)
			const pages=await Promise.all(promises)
			const viewports=generateViewports(pages,1.5)
			// console.log(pages,viewports)
			setPdf({pdf,pages,viewports})
		}
	}

	// re-use when zooming
	function generateViewports(pages,scale){
		return pages.map(p=>p.getViewport({scale}))
	}

	return (
		<div id="PageBook" className="pos-abs stretch-to-fit f-vert">
			<section className="row main-section">
				<div className="col left-sidebar pos-rel">
					<PdfVerticalScroll {...{pdf,publicPrivateMode,selection,book}}/>
					<Spinner className={pdf?.pdf?'':'active'}/>
					<Route path="*/new-comment" render={props=><NewQuestionComment {...{props,selection,book,sidebarMode,}}/>}/>
				</div>
				<div className="right-sidebar row pos-rel p-12 pl-0">
					<div className="vertical-tabs f-vert">
						<div className="col" style={{maxHeight: '14px'}}/>
						{publicPrivateMode==='public'?<>
							<div className={sidebarMode==='question'?'active':''} onClick={()=>setSidebarMode('question')}>
								<img src={tabStart} alt=""/>
								<div className="vertical-text px-4">Questions <span className="h7">
									{categorized['question'].length}
								</span></div>
								<img src={tabStart} alt=""/>
							</div>
							<div className={sidebarMode==='comment'?'active':''} onClick={()=>setSidebarMode('comment')}>
								<img src={tabStart} alt=""/>
								<div className="vertical-text px-4">Comments <span className="h7">
									{categorized['comment'].length}
								</span></div>
								<img src={tabStart} alt=""/>
							</div>
						</>:<>
							<div className={sidebarMode==='note'?'active':''} onClick={()=>setSidebarMode('note')}>
								<img src={tabStart} alt=""/>
								<div className="vertical-text px-4">Notes <span className="h7">
									{categorized['note'].length}
								</span></div>
								<img src={tabStart} alt=""/>
							</div>
							<div className={sidebarMode==='highlight'?'active':''} onClick={()=>setSidebarMode('highlight')}>
								<img src={tabStart} alt=""/>
								<div className="vertical-text px-4">Highlights <span className="h7">
									{categorized['highlight'].length}
								</span></div>
								<img src={tabStart} alt=""/>
							</div>
						</>}
						<span className="col"/>
					</div>
					<div className="col trim">
						<Scrollbar disableTracksWidthCompensation>
							<div className="row spc-12 f-vert">
								{filtered.map(c=><Comment key={c.id} {...{commentId: c.id}}/>)}
								<div style={{height: '100vh'}}/>
								<div className="col-ext-x col-ext-t pos-sty top-controls f-order-m1 f-vert">
									{sidebarMode!=='highlight'?<div className="row spc-12 f-vert">
										{publicPrivateMode==='public'?<SegmentControls value={+userFilter}>
											<div className="super-center capitalize" onClick={()=>setUserFilter(false)}>All {sidebarMode}s</div>
											<div className="super-center capitalize" onClick={()=>setUserFilter(true)}>My {sidebarMode}s</div>
										</SegmentControls>:undefined}
										<input type="text" placeholder=" &#xf002;  filter by hashtag" onChange={e=>setPublicSearchString(e.target.value)}/>
									</div>:undefined}
									<div className="mx-auto pill mt-8 mb-m6 px-10 py-2 h7">{filtered.length} results</div>
								</div>
							</div>
						</Scrollbar>
					</div>
				</div>
			</section>
			<Header>
				<div className="icon icon-arrow_left f-order-m1 super-center" onClick={history.goBack}/>
				<div className="col"/>
				<SegmentControls className="mr-12" value={publicPrivateMode==='public'?1:0} style={{width: '200px'}}>
					<div className="super-center capitalize" onClick={()=>setSidebarMode('note')}>Private</div>
					<div className="super-center capitalize" onClick={()=>setSidebarMode('comment')}>Public</div>
				</SegmentControls>
				<div className="col-ext row spc-x-8" style={{maxWidth: 'var(--rightSidebarWidth)'}}>
					<div className="col text-right mr-12 one-line">
						<h5 className="col-12 b-700 one-line">{book?book.title:'PageBook Title'}</h5>
						<div className="h7 text-muted one-line">Group: {group?group.title:'Group Title'}</div>
					</div>
					<div className="icon icon-refresh super-center" onClick={refresh}/>
				</div>
			</Header>
			<Route path="*/thread/:id" component={CommentThread}/>
		</div>
	)
})

export let setBubblePos
export const NewCommentBubble=withRouter(function({location: {pathname},publicPrivateMode,selection,book}){
	const {state,dispatch}=useContext(MainContext)
	const {userId}=state
	let bubblePos
	;[bubblePos,setBubblePos]=useState({})
	return(
		<div className="new-bubble pos-abs f-end f-justify-center h7 pt-4" style={bubblePos}>
			{publicPrivateMode==='public'?<>
				<Link to={pathname+'/new-comment'} className="py-4 px-8 text-right" onClick={()=>setSidebarMode('question')}>Question</Link>
				<Link to={pathname+'/new-comment'} className="py-4 px-8" onClick={()=>setSidebarMode('comment')}>Comment</Link>
			</>:<>
				<Link to={pathname+'/new-comment'} className="py-4 px-8 text-right" onClick={()=>setSidebarMode('note')}>Note</Link>
				<a className="py-4 px-8" onClick={addHighlight}>Highlight</a>
			</>}
		</div>
	)

	function addHighlight(){
		setSidebarMode('highlight')
		api('post','comment/',{
			body: 'Highlight',
			hashtag: '',
			selection_data: JSON.stringify([selection]),
			user: userId,
			book: book.id,
			typeof: 'highlight',
		}).then(({data})=>{
			book.comment_set.push(data.id)
			dispatch(['SET_DATA',{comments: [data],books: [book]}])
			setSelection(undefined)
			setBubblePos({})
		})
	}
})
