import {PureComponent} from 'react'

export default class MyComponent extends PureComponent{
	// Used for simple 2-way binding
	// eg: <input {...this.twoWayBind('stateVariable')}/> will return
	// <input value={this.state['stateVariable']} onChange={e=>this.setState({'stateVariable':e.target.value}}/>
	// TODO: Finish example for different params
	twoWayBind=(property,event='onChange',valAttr='value')=>({
		[valAttr]:this.state[property],
		[event]:e=>this.setState({[property]:e.target[valAttr]}),
	})
}