import React from 'react'
import {withRouter} from 'react-router-dom'

import './NewQuestionComment.scss'
import MyComponent from '../MyComponent'
import {MainContext,nonAlphaNumericRegex,api} from '../../contexts/MainContext'

const initState=JSON.stringify({
	body: '',
	hashtag: '',
})
export default withRouter(class NewQuestionComment extends MyComponent{
	static contextType=MainContext
	state=JSON.parse(initState)

	onSubmit=e=>{
		e.preventDefault()

		const {sidebarMode,selection,book,history}=this.props
		const {body,hashtag}=this.state
		const {userId}=this.context.state

		api('post','comment/',{
			body,
			hashtag: hashtag.replace(nonAlphaNumericRegex,'').trim(),
			selection_data: JSON.stringify([selection]),
			user: userId,
			book: book.id,
			typeof: sidebarMode,
		}).then(({data})=>{
			book.comment_set.push(data.id)
			this.context.dispatch(['SET_DATA',{comments: [data],books: [book]}])
			// TODO: resetSelection
			history.goBack()
		})
	}

	render(){
		const {onSubmit}=this
		const {sidebarMode,history,selection}=this.props

		return (
			<div className="NewQuestionComment modal active pos-abs stretch-to-fit p-12 f-end f-justify-center" style={{}}>
				<form className="row spc-12" style={{width: '480px'}} {...{onSubmit}}>
					<div className="close-btn" onClick={history.goBack}/>
					<div className="capitalize">New {sidebarMode}</div>
					<blockquote className="col-12 h7 text-muted mt-2 row">
						<div className="trim-y col">{selection?.text}</div>
					</blockquote>
					<textarea required className="col-12" placeholder={sidebarMode} {...this.twoWayBind('body')}/>
					<div className="col-12"/>
					<div className="bottom-row col-12 col-ext row spc-12">
						<input className="col" type="text" placeholder="#Hashtag" {...this.twoWayBind('hashtag')}/>
						<button>Post</button>
					</div>
				</form>
			</div>
		)
	}
})
