import React,{useContext,useEffect,useState} from 'react'
import {withRouter} from 'react-router-dom'
import {Scrollbar} from 'react-scrollbars-custom'

import './PageGroup.scss'
import Header from '../Header/Header'
import {MainContext,api} from '../../contexts/MainContext'
import Group from '../Group/Group'

export default withRouter(function PageGroup({location,history,match}){
	const {state,dispatch}=useContext(MainContext)
	useEffect(refresh,[location.pathname])
	const [showJoinDialog,setShowJoinDialog]=useState(true)
	const groupId=match.params.id
	const group=state.GROUPS[groupId]
	const user=state.USERS[state.userId]

	console.log(history)

	function back(){
		history.length>1?
			history.goBack():
			history.replace('/groups')
	}

	function refresh(){
		if(!state.userId) return
		api('get','group-full/'+match.params.id).then(({data})=>{
			dispatch(['SET_DATA',data])
		}).catch(console.error)
	}

	return (
		<div id="PageGroup">
			<Scrollbar id="PageGroup" className="pos-abs stretch-to-fit">
				<div className="f-vert">
					<div className="container">
						{group?<Group {...{key:groupId,group}}/>:undefined}
					</div>
					<Header>
						<div className="icon icon-arrow_left f-order-m1 super-center" onClick={back}/>
						<div className="col"/>
						<div className="icon icon-refresh super-center" onClick={refresh}/>
					</Header>
				</div>
			</Scrollbar>
			{(showJoinDialog && user.joined_groups.indexOf(groupId)===-1)?<div className="container p-12 pos-abs join-group stretch-to-fit">
				<div className="card row spc-12 f-center">
					<div>Do you want to join this group?</div>
					<div className="col"/>
					<button className="outline" style={{width: '60px'}} onClick={()=>setShowJoinDialog(false)}>No</button>
					<button style={{width: '60px'}}>Yes</button>
				</div>
			</div>:undefined}
		</div>
	)
})
