import React,{memo,useState} from 'react'
import * as pdfjs from 'pdfjs-dist'
import _ from 'lodash'

import Spinner from '../Spinner/Spinner'

pdfjs.GlobalWorkerOptions.workerSrc=`//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

export default pdfjs

export const PdfImage=memo(function({page,viewport}){
	const [data,setData]=useState(undefined)
	if(!data){
		const canvas=document.createElement('canvas')
		const canvasContext=canvas.getContext('2d')
		canvas.height=viewport.height
		canvas.width=viewport.width

		page.render({canvasContext,viewport}).promise.then(()=>setData(canvas.toDataURL()))
		return <Spinner className="active"/> // TODO: Change spinner background to white
	}else
		return <img src={data} alt=""/>
})

export const TextLayer=memo(function({page,viewport}){
	const [data,setData]=useState(undefined)
	if(!data){
		page.getTextContent().then(textContent=>{
			// console.log(textContent)
			const textDiv=document.createElement('div')
			pdfjs.renderTextLayer({
				textContent,
				container: textDiv,
				viewport,
				textDivs:  [],
			}).promise.then(()=>setData(textDiv.innerHTML)).catch(console.error)
			// console.log(textDiv,textDiv.outerHTML)
			// setData(textDiv.outerHTML)
		}).catch(console.error)
		return <Spinner className="active"/>
	}else
		return <div className="text-layer pos-abs stretch-to-fit" dangerouslySetInnerHTML={{__html: data}}/>
})

