import React,{Fragment} from 'react'
// import _ from 'lodash'

import './SegmentControls.scss'

// TODO: Focus
export default function SegmentControls({value,className,children,vertical,...props}){
	const val=Number.isInteger(value)?value:-1
	const sizeAttr=vertical?'height':'width'
	return (
		<div {...props} className={'segment-controls row '+(className || '')}>
			<div
				className="highlight"
				style={{
					transform: vertical?`translateY(${val*100}%)`:`translateX(${val*100}%)`,
					[sizeAttr]: 100/children.length+'%',
				}}
			/>
			{children.map((c,i)=><Fragment key={i}>{c}</Fragment>)}
		</div>
	)
}
