import React from 'react'
import {Route,Switch,withRouter} from 'react-router-dom'

import './App.scss'
import {dispatch} from '../../contexts/MainContext'
import Sidemenu from '../Sidemenu/Sidemenu'
import Dashboard from '../PageDashboard/PageDashboard'
import Book from '../PageBook/PageBook'
import PageLogin from '../PageLogin/PageLogin'
import Alert from '../../utils/Alert/Alert'
import Groups from '../PageGroups/PageGroups'
import PageRegister from '../PageRegister/PageRegister'
import ModalEditGroup from '../ModalEditGroup/ModalEditGroup'
import PageGroup from '../PageGroup/PageGroup'

export let forceUpdateApp
export default withRouter(function App({location,history}){
	const [,updateState]=React.useState()
	forceUpdateApp=()=>updateState({})

	if(location.pathname==='/')
		history.replace('/dashboard')
	if(!localStorage.getItem('activeUserId') && location.pathname.indexOf('/login')===-1) // Routes containing /login can appear without token present
		history.replace(location.pathname+'/login')

	// console.log('App Render')
	return (
		<div id="App">
			<Sidemenu/>
			<div id="main-content" className="pos-abs stretch-to-fit">
				<Switch>
					{/* Level 2 */}

					{/* Level 1 */}
					<Route path="*/group/:id" component={PageGroup}/>

					{/* Level 0 */}
					<Route path="/dashboard" component={Dashboard}/>
					<Route path="/groups" component={Groups}/>
				</Switch>
				<Route path="*/edit-group/:id" component={ModalEditGroup}/>
			</div>
			<Route path="*/book/:id" component={Book}/>

			{/* Routes containing /login can appear without token present */}
			<Route path="*/login" component={PageLogin}/>
			<Route path="*/login-register:id" component={PageRegister}/>

			{/* Activated when Sidemenu active to prevent clicks over main-content */}
			<div
				id="shield"
				className="hide pos-abs stretch-to-fit"
				onClick={()=>dispatch(['SET_UI_SIDEMENU',false])}
			/>
			<Alert/>
		</div>
	)
})
