import React,{useContext,useEffect} from 'react'
import {withRouter} from 'react-router-dom'
import {Scrollbar} from 'react-scrollbars-custom'

import './PageDashboard.scss'
import Header from '../Header/Header'
import {MainContext,api} from '../../contexts/MainContext'
import Group from '../Group/Group'

export default withRouter(function PageDashboard({location}){
	const {state,dispatch}=useContext(MainContext)
	useEffect(refresh,[location.pathname]) // TODO: Test userId instead of pathname
	const user=state.USERS[state.userId]

	function refresh(){
		if(!state.userId) return
		api('get','dashboard/'+state.userId).then(({data})=>{
			dispatch(['SET_DATA',data])
		}).catch(console.error)
	}

	return (
		<Scrollbar id="PageDashboard" className="pos-abs stretch-to-fit">
			<div className="f-vert">
				<div className="container">
					{(user?.joined_groups || []).map(groupId=>{
						const group=state.GROUPS[groupId]
						return group?(
							<Group {...{key:groupId,group}}/>
						):undefined
					})}
				</div>
				<Header>
					<div className="col"/>
					<div className="icon icon-refresh super-center" onClick={refresh}/>
				</Header>
			</div>
		</Scrollbar>
	)
})
