import React,{createRef} from 'react'
// import _ from 'lodash'
import {Scrollbar} from 'react-scrollbars-custom'

import './PageRegister.scss'
import MyComponent from '../MyComponent'
import {MainContext,/*api*/} from '../../contexts/MainContext'
import Spinner from '../../utils/Spinner/Spinner'
// import {setAlert} from '../../utils/Alert/Alert'

const initState=JSON.stringify({
	showSpinner: false,
})
export default class PageRegister extends MyComponent{
	static contextType=MainContext
	state=JSON.parse(initState)

	constructor(props){
		super(props)
		this.ref=createRef()
	}

	componentDidMount=()=>{
		const refEle=this.ref.current.holderElement
		console.log(refEle.querySelector('form').style.minHeight=refEle.offsetHeight+'px')
	}

	// TODO: Regex
	render(){
		const {ref}=this
		const {showSpinner,email}=this.state
		const {version}=this.context.state
		return (
			<div id="PageRegister" className="pos-abs stretch-to-fit">
				<div className="container row p-32">
					<div className="col">
						<h1 className="heading py-32">
							BBBook<sup className="h7 normal-caps ml-8" style={{height: 'auto'}}>Pre-Alpha {version}</sup>
						</h1>
					</div>
					<Scrollbar className="card" style={{width: '360px'}} {...{ref}}>
						<form className="row spc-16 f-vert col col-ext pos-rel trim">
							<div>
								<h1>Sign Up</h1>
								<div className="h7 text-muted">Complete your details.</div>
							</div>
							<div className="col"/>
							<label className="h7 my-0">Email</label>
							<input required readOnly name="email" type="email" value={email}/>
							<label required className="h7 my-0">Username</label>
							<input required {...this.twoWayBind('username')} name="username" type="text"/>
							<label required className="h7 my-0">First Name</label>
							<input required {...this.twoWayBind('first_name')} name="first_name" type="text"/>
							<label required className="h7 my-0">Last Name</label>
							<input required {...this.twoWayBind('last_name')} name="last_name" type="text"/>
							<label required className="h7 my-0">Password</label>
							<input required {...this.twoWayBind('password')} name="password" type="password"/>
							<label required className="h7 my-0">Confirm Password</label>
							<input required {...this.twoWayBind('password_2')} name="password_2" type="password"/>
							<label required className="h7 my-0">Date of Birth</label>
							<input required {...this.twoWayBind('birth_date')} name="birth_date" type="date"/>
							<label className="h7 my-0">Bio</label>
							<textarea {...this.twoWayBind('bio')} name="bio"/>
							{/* TODO: Avatar	*/}
							<div className="bottom-row col-ext-x col-ext-b text-right row f-vert spc-x-16 spc-y-12">
								<button>Complete</button>
								{/*<hr className="col-ext-x"/>
								<div className="h7 text-right my-2" onClick={()=>this.setState({mode: 1})}>New to BBBook? Sign up!</div>*/}
							</div>
						</form>
					</Scrollbar>
				</div>
				<Spinner {...{className: showSpinner?'active':''}}/>
			</div>
		)
	}
}
