import React from 'react'
import {withRouter} from 'react-router-dom'
import {Scrollbar} from 'react-scrollbars-custom'

import './CommentThread.scss'
import Comment from '../Comment/Comment'
import MyComponent from '../MyComponent'
import {MainContext,nonAlphaNumericRegex,api} from '../../contexts/MainContext'

const initState=JSON.stringify({
	body: '',
	hashtag: '',
})
export default withRouter(class CommentThread extends MyComponent{
	static contextType=MainContext
	state=JSON.parse(initState)

	onSubmit=e=>{
		e.preventDefault()
		const commentId=this.props.match.params.id
		const {body,hashtag}=this.state
		const {COMMENTS,BOOKS,userId}=this.context.state
		const comment=COMMENTS[commentId]
		api('post','comment/',{
			body,
			hashtag: hashtag.replace(nonAlphaNumericRegex,'').trim(),
			user: userId,
			book: comment.book,
			replyto: commentId,
			typeof: 'reply'
		}).then(({data})=>{
			COMMENTS[commentId].replys.push(data.id)
			console.log({comments: [data,COMMENTS[commentId]]})
			const book=BOOKS[comment.book]
			book.comment_set.push(data.id)
			this.context.dispatch(['SET_DATA',{comments: [data,COMMENTS[commentId]],books: [book]}])
			this.setState(JSON.parse(initState))
		})
	}

	render(){
		// console.log(this.props)
		const {onSubmit}=this
		const {history,match}=this.props
		const commentId=match.params.id
		const {userId,USERS}=this.context.state

		return (
			<div className="CommentThread pos-abs stretch-to-fit row spc-12 f-justify-end">
				<div className="p-12 f-start f-justify-end f-self-start">
					<div className="h5 icon icon-cross_mark" onClick={history.goBack} style={{cursor: 'pointer'}}/>
				</div>
				<Scrollbar disableTracksWidthCompensation className="thread-container col" style={{height: 'auto'}}>
					<div className="row spc-12 f-vert pos-rel">
						<Comment {...{commentId,showChildren: true}}>
							<div className="reply-input">
								<div className="top-row row pb-4 f-justify-end">
									<div className="col"/>
									<div className="h7 text-muted">@{USERS[userId]?.username || 'Username'}</div>
									<img className="pos-rel" src={require('../../assets/imgs/default-profile-icon.jpg')} alt="User icon"/>
								</div>
								<form className="form row spc-12" {...{onSubmit}}>
									<div className="h7 col-12 my-2">Add reply</div>
									<textarea required className="col-12" cols="30" rows="10" placeholder="Reply" {...this.twoWayBind('body')}/>
									<input type="text" className="col" placeholder="#Hashtag" {...this.twoWayBind('hashtag')}/>
									<button>Post</button>
								</form>
							</div>
						</Comment>
						{/*<div style={{height: '1000px'}}/>*/}
					</div>
				</Scrollbar>
			</div>
		)
	}
})
