import React from 'react'
import {Link} from 'react-router-dom'
import _ from 'lodash'
import {Scrollbar} from 'react-scrollbars-custom'

import './ModalEditGroup.scss'
import MyComponent from '../MyComponent'
import SegmentControls from '../../utils/SegmentControls/SegmentControls'
import {MainContext,api} from '../../contexts/MainContext'
import Spinner from '../../utils/Spinner/Spinner'

// TODO: Admin revoked - alert
// TODO: ManageUsers Component
export default class ModalEditGroup extends MyComponent{
	static contextType=MainContext
	// TODO: Take it outside state
	state={
		publicPrivate: 1,
		linkSharing: 1,
		showSpinner: false,
	}
	admins={}

	render(){
		const {onSubmit,admins}=this
		const {location,history}=this.props
		const {publicPrivate,linkSharing,showSpinner}=this.state
		const {id}=this.props.match.params
		const {userId,GROUPS,USERS}=this.context.state
		const group=GROUPS[id]

		if(!userId) return null
		if(id!=='0'){
			if(!group) return null
			if(this.state.id!==id) this.groupTypeToState(group)
		}

		const manageUsersMode=location.pathname.indexOf('/edit-group-users')!==-1
		return (
			<div id="ModalEditGroup" className="modal active">
				<form className={manageUsersMode?'expanded':''} {...{onSubmit}}>
					<div className="row spc-12">
						<h3 className="col">{id==='0'?'New':'Edit'} Group</h3>
						{/* TODO: Show group details in manageUsersMode */}
						{/*<div><button className="outline" style={{width: '68px',height: '68px',marginBottom: '-45px'}}/></div>*/}
						<hr className="col-12 col-ext-x col-ext-b"/>
					</div>
					{!manageUsersMode?
						<div className="row spc-12">
							<div className="col">
								<label required className="h7 mb-4">Group Name</label>
								<input required className="col-12" type="text" {...this.twoWayBind('title')}/>
							</div>
							<div className="col-12">
								<label className="h7 mb-4">Group Description</label>
								<textarea className="col-12" {...this.twoWayBind('bio')}/>
							</div>
							<div className="col-6">
								<label className="h7 mb-4">Group Privacy</label>
								<SegmentControls value={publicPrivate}>
									<div className="super-center" onClick={()=>this.setState({publicPrivate: 0,linkSharing: 0})}>Public</div>
									<div className="super-center" onClick={()=>this.setState({publicPrivate: 1})}>Private</div>
								</SegmentControls>
							</div>
							<div className="col-6">
								<label className="h7 mb-4" disabled={publicPrivate===0}>Invite via Link</label>
								<SegmentControls value={linkSharing} disabled={publicPrivate===0}>
									<div className="super-center" onClick={()=>this.setState({linkSharing: 0})}>Enabled</div>
									<div className="super-center" onClick={()=>this.setState({linkSharing: 1})}>Disabled</div>
								</SegmentControls>
							</div>
							{/* TODO: Green accent for link sharing enabled */}
							{id!=='0'?<div className="col-12 f-vert">
								<label className="h7 mb-4">Link</label>
								<Link
									to={'/groups/group/'+id}
									target="_blank"
									className="col-12 button outline d-block one-line"
									type="text" {...this.twoWayBind('title')}>
									{window.location.origin+'/#/groups/group/'+id}
								</Link>
								<div className="button ml-auto h7 mt-6" style={{height: 'auto'}}>Copy Link</div>
							</div>:undefined}
						</div>:
						<Scrollbar style={{height: 'calc(100% - 114px)'}}>
							<div className="row spc-12 container">
								<div className="col-12 row f-center">
									Admins <hr className="col ml-16"/>
								</div>
								<div className="col-12 row f-center f-order-1">
									Users <hr className="col ml-16"/>
								</div>
								{group.users.map(u=>{
									const user=USERS[u]
									console.log(admins,admins[u])
									return user?<div className={'card col-3 row spc-12 f-center'+(admins[u]?'':' f-order-1')}>
										<img
											className="card col-ext-y col-ext-l"
											src={require('../../assets/imgs/default-profile-icon.jpg')}
											style={{width: '61px',height: '61px',borderRadius: '8px 0 0 8px'}}
											alt="user profile pic"/>
										<div className="col one-line">
											{user.first_name || 'First Name'} {user.last_name || 'Last Name'}
											<div className="h7 text-muted mt-2">@{user.username} | {user.email}</div>
										</div>
									</div>:undefined
								})}
							</div>
						</Scrollbar>
					}
					<div className="bottom-row col-12 col-ext row spc-12">
						{(id!=='0' && !manageUsersMode)?<>
							<Link
								to={location.pathname+'/edit-group-users'}
								className="button super-center">Manage Users</Link>
							<div className="f-self-center h7">
								<i className="icon icon-users"/> {group?.users.length}
							</div>
						</>:undefined}
						<div className="col"/>
						<div className="button outline super-center" onClick={history.goBack}>Cancel</div>
						<button>{id==='0'?'Create Group':'Save'}</button>
					</div>
					<Spinner className={showSpinner?'active':''}/>
					<div className="close-btn" onClick={history.goBack}/>
				</form>
			</div>
		)
	}

	// TODO: Avatar
	// TODO: Replace _.pick
	onSubmit=async e=>{
		e.preventDefault()
		const {id}=this.props.match.params
		const {state}=this.context
		const {userId}=state
		let group={
			..._.omit(this.state,['publicPrivate','linkSharing','showSpinner']),
			group_type: this.stateToGroupType(),
		}

		this.setState({showSpinner: true})
		try{
			if(id==='0'){
				group={
					...group,
					users: [userId],
					admins: [userId],
					books: [],
				}
				/*const g=*/await api('post','group/',group)
				// TODO: Backend /group-full
				this.props.history.goBack() // TODO: Remove
			}else{
				/*const g=*/await api('put','group/',group)
				// TODO: Backend /group-full
			}
		}finally{
			this.setState({showSpinner: false})
		}
	}

	// Group types
	// - public       -     searchable,    link-admit
	// - private      - non-searchable, no link-admit
	// - private-link - non-searchable,    link-admit

	groupTypeToState=group=>{
		this.admins={}
		group.admins.forEach(a=>this.admins[a]=true)
		this.setState({
			..._.cloneDeep(group),
			publicPrivate: group.group_type==='public'?0:1,
			linkSharing: group.group_type!=='private'?0:1,
		})
	}

	stateToGroupType=()=>{
		const {publicPrivate,linkSharing}=this.state
		if(publicPrivate===0) return 'public'
		else if(linkSharing===0) return 'private-link'
		else return 'private'
	}
}
