import React,{createContext,useReducer,useState} from 'react'
import {withRouter} from 'react-router-dom'
import axios from 'axios'
// import _ from 'lodash'

import mainReducer,{initState,state} from '../reducers/mainReducer'
import {setAlert} from '../utils/Alert/Alert'
import _ from 'lodash'

window.axios=axios
export const MainContext=createContext(JSON.parse(initState))
export const nonAlphaNumericRegex=/[^A-Za-z0-9 ]+/g
export let dispatch

export default withRouter(function MainContextProvider({children,location,history}){
	let state
	([state,dispatch]=useReducer(mainReducer,JSON.parse(initState)))
	const [mounted,setMounted]=useState(false)

	if(localStorage.getItem('activeUserId') && !mounted)
		dispatch(['RE_LOGIN'])

	if(!mounted) setMounted(true)
	return (
		<MainContext.Provider value={{state,dispatch}}>{children}</MainContext.Provider>
	)
})

const UNAUTHORIZED=401
export async function api(method,url,data){
	// console.log(method,url,data)
	let {access_token}=state
	if(access_token){
		try{
			await axios.post(state.url+'token/verify/',{token: access_token})
		}catch(e){
			if(e.response.status===UNAUTHORIZED){ // If access_token invalid, refresh
				try{
					const r=await axios.post(state.url+'token/refresh/',{refresh: state.refresh_token})
					access_token=r.data.access
					dispatch(['REFRESH',access_token])
				}catch(e){
					if(e.response.status===UNAUTHORIZED){ // If refresh_token invalid, logout
						setAlert(<div className="row spc-12" style={{width: 320}}>
							<div className="col-12 h5">&#xf071; Error</div>
							<div className="col-12 h7 text-muted">
								You were logged out. Please login again...
							</div>
							<div className="bottom-row col-ext-x col-ext-b col-12 row spc-x-12 spc-y-8 f-justify-end">
								<button onClick={()=>setAlert()}>Close</button>
							</div>
						</div>)
						dispatch(['LOGOUT'])
						throw new Error('refresh_token expired')
					}else onError(e)
				}
			}else onError(e)
		}
	}

	try{
		const options={
			url: state.url+url,
			method,
			data,
		}
		if(access_token) options.headers={Authorization: 'Bearer '+access_token}
		return await axios(options)
	}catch(e){ onError(e) }

	function onError(e){
		console.log(e,e.response)
		setAlert(<div className="row spc-12" style={{width: 320}}>
			<div className="col-12 h5">&#xf071; Error</div>
			<div className="col-12 h7 text-muted">
				{e.response?.data?
					Object.keys(e.response.data).map(key=><div {...{key}}>{_.capitalize(key)}: {e.response.data[key]}</div>):
					<div>Error: Network Error</div>}
			</div>
			<div className="bottom-row col-ext-x col-ext-b col-12 row spc-x-12 spc-y-8 f-justify-end">
				<button onClick={()=>setAlert()}>Close</button>
			</div>
		</div>)
		throw e
	}
}
