// import _ from 'lodash'

import {forceUpdateApp} from '../components/App/App'

const mode=0
const modes=[
	{
		mode: 'dev',
		url: 'https://bbapi-testing.herokuapp.com/bbapi/',
	},
	{
		mode: 'prod',
		url: 'https://test-for-bbbook.herokuapp.com/bbapi/',
	},
]
export const initState=JSON.stringify({
	access_token: null,
	refresh_token: null,
	userId: null,

	USERS:    {},
	GROUPS:   {},
	BOOKS:    {},
	COMMENTS: {},

	UI_SIDEMENU: false,
	UI_DARKMODE: 'auto', // 'auto',true,false

	version: '0.0.10 '+modes[mode].mode,
	...modes[mode]
})
export let state=JSON.parse(initState)

export default function mainReducer(oldState,[actionType,data]){
	state={...state,...reducer(oldState,actionType,data)}
	if(mode===0) window.state=state
	return state
}

function reducer(state,actionType,data){
	let userId,loggedInUsers
	switch(actionType){
		case 'SET_STATE': return data
		case 'SET_UI_SIDEMENU': return {UI_SIDEMENU: data}
		case 'SET_UI_DARKMODE': return {UI_DARKMODE: data}
		case 'SET_DATA':
			const cache={
				USERS   : {...state.USERS   ,...(reduce(data.users    ?? []))}, // eslint-disable-line no-whitespace-before-property
				GROUPS  : {...state.GROUPS  ,...(reduce(data.groups   ?? []))}, // eslint-disable-line no-whitespace-before-property
				BOOKS   : {...state.BOOKS   ,...(reduce(data.books    ?? []))}, // eslint-disable-line no-whitespace-before-property
				COMMENTS: {...state.COMMENTS,...(reduce(data.comments ?? []))}, // eslint-disable-line no-whitespace-before-property
			}
			localStorage.setItem('cache',JSON.stringify(cache)) // TODO: Cache size limiting, preserve logged in users
			return cache
		case 'LOGIN':
			const {access_token,refresh_token}=data
			userId=data.user.id
			localStorage.setItem('activeUserId',userId)
			localStorage.setItem('loggedInUsers',JSON.stringify({
				...JSON.parse(localStorage.getItem('loggedInUsers') || '{}'),
				[userId]: [access_token,refresh_token]
			}))
			return {
				access_token,
				refresh_token,
				userId,
				...reducer(state,'SET_DATA',{users: [data.user]}),
			}
		case 'REFRESH':
			loggedInUsers=JSON.parse(localStorage.getItem('loggedInUsers'))
			loggedInUsers[state.userId][0]=data
			localStorage.setItem('loggedInUsers',JSON.stringify(loggedInUsers))
			return {access_token: data}
		case 'RE_LOGIN':
			userId=localStorage.getItem('activeUserId')
			const login=JSON.parse(localStorage.getItem('loggedInUsers'))[userId]
			return{
				access_token: login[0],
				refresh_token: login[1],
				userId,
				...JSON.parse(localStorage.getItem('cache'))
			}
		case 'LOGOUT':
			localStorage.removeItem('activeUserId')
			loggedInUsers=JSON.parse(localStorage.getItem('loggedInUsers'))
			delete loggedInUsers[state.userId]
			localStorage.setItem('loggedInUsers',JSON.stringify(loggedInUsers))
			forceUpdateApp()
			return JSON.parse(initState)
		default: return {}
	}
}

function reduce(data){
	const lib={}
	data.forEach(d=>lib[d.id]=d)
	return lib
}
